.All-Instructor-grid-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr); 
  grid-gap: 20px; 
}

.All-Instructor-grid-container li {
  list-style: none;
}

.All-Instructor-grid-item {
  background-color: #fff; 
  padding: 20px 0px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); 
  min-width: 200px; 
  min-height: 300px; 
}

.All-Instructor-fcrse_1 {
  display: flex;
  flex-direction: column;
  background: #fff;
  overflow: hidden;
  width: 100%;
  margin-bottom: 20px;
}

.All-Instructor-tutor_img {
  width: 100%;
  text-align: center;
}

.All-Instructor-tutor_img img {
  width: 100px;
  height: 100px;
  border-radius: 100%;
  border: 2px solid #fff;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.1);
}

.All-Instructor-tutor_content_dt {
