/* AddLiveStream.css */

/* Container */
.Live-container-fluid {
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
    justify-content: center;
    align-items: center; 
    min-height: 100vh; 
    user-select: none;
}

  /* Row */
  .row {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
  }
  
  /* Columns */
  .col-lg-8, .col-md-10 {
    grid-column: span 1;
    justify-self: center;
  }
  


