.ui.toggle.checkbox {
    display: flex;
    align-items: center;
    margin: 10px 0;
}

.ui.toggle.checkbox input[type="checkbox"] {
    display: none; /* n checkbox  s dng custom toggle */
}

.ui.toggle.checkbox label {
    cursor: pointer;
    display: inline-block;
    position: relative;
    padding-left: 50px;
    line-height: 24px;
    font-size: 14px;
}

.ui.toggle.checkbox label:before,
.ui.toggle.checkbox label:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 40px;
    height: 24px;
    transition: background-color 0.4s, transform 0.4s;
}

.ui.toggle.checkbox label:before {
    background-color: #ccc;
    border-radius: 12px;
}

.ui.toggle.checkbox label:after {
    background-color: #fff;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    top: 2px;
    left: 2px;
}

/* X l khi checkbox c chn */
.ui.toggle.checkbox input[type="checkbox"]:checked + label:before {
    background-color: red;
}

.ui.toggle.checkbox input[type="checkbox"]:checked + label:after {
    transform: translateX(16px);
}

/* X l khi checkbox cha c chn */
.ui.toggle.checkbox input[type="checkbox"]:not(:checked) + label:before {
    background-color: #ccc;
}

.ui.toggle.checkbox input[type="checkbox"]:not(:checked) + label:after {
    transform: translateX(0);
}

/* X l khi checkbox c nhn */
.ui.toggle.checkbox input[type="checkbox"]:active + label:before {
    background-color: blue;
}




