.Sort_Tbl {
    float: left;
    width: 100%;
    text-align: left;
}

.SR_Tbl_Content {
    margin-bottom: 20px;
    margin-top: 20px;
}

.SR_Tbl_Content>div {
    display: flex;
    justify-content: space-between;
    /* align-items: center; */
    cursor: pointer;
    padding: 5px 10px;
    border-bottom: 1px solid #efefef;
    border-radius: 4px;
    /* background-color: #f0f0f0; */
}

.SR_Tbl_Content>div>span {
    font-weight: bold;
}

.SR_Tbl_Content_Topic_Menu {
    display: none;
    flex-direction: column;
    /* Hin th cc label theo chiu dc */
    padding: 10px;
    margin-top: 5px;
}

.SR_Tbl_Content_Topic_Menu>div.active>div {
    display: block;
}

.SR_Tbl_Content_Topic_Menu label {
    display: block;
    margin-bottom: 5px;
}

.SR_Tbl_Content_Topic_Menu input[type="radio"] {
    margin-right: 5px;
}

.RS_starIcon {
    width: 15px !important;
    height: 15px !important;
    top: 1px;
    position: relative;
    margin-right: 5px;
}